html {
  box-sizing: border-box;
  font-size: 100%;
  font-family: var(--font-body);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  padding: 0;
  margin: 0;
  font-size: 1.125rem;
  font-weight: 700;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --very-dk-gray: hsl(0, 0%, 17%);
  --dk-gray: hsl(0, 0%, 59%);
}

:root {
  --font-body: "Rubik", sans-serif;
}

.container {
  width: 100%;
  padding-inline: 1.5625rem;
  padding-block: 2rem;
}
@media (min-width: 71.875em) {
  .container {
    max-width: 69.4375rem;
    margin: 0 auto;
    padding-inline: 0;
  }
}

a:link,
a:visited,
a:hover {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

header {
  height: 18.8125rem;
  text-align: center;
  background-image: url("/images/pattern-bg.png");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
@media (min-width: 71.875em) {
  header {
    height: 17.5rem;
    background-size: 100% 100%;
  }
}

section h1 {
  margin-bottom: 2.1875rem;
  font-size: 1.25rem;
  color: var(--white);
}
section form {
  margin-bottom: 1.5rem;
}
section form label {
  display: inline-block;
  position: relative;
  height: 100%;
  width: 100%;
}
@media (min-width: 40em) {
  section form label {
    width: 34.8125rem;
  }
}
section form input[type=text] {
  width: 100%;
  outline: none;
  border: none;
  border-radius: 1.25rem;
  padding-block: 1.28125rem;
  padding-left: 1.375rem;
  padding-right: 3.75rem;
  font-size: 0.9375rem;
}
section form button {
  width: 3.625rem;
  height: 100%;
  outline: none;
  border: none;
  border-top-right-radius: 1.25rem;
  border-bottom-right-radius: 1.25rem;
  position: absolute;
  right: 0;
  background-color: var(--very-dk-gray);
  cursor: pointer;
}
section form button::before {
  content: url("/images/icon-arrow.svg");
  display: block;
  width: 0.6875rem;
  height: 0.9375rem;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);
}
section .info-ip {
  border-radius: 1.25rem;
  padding-block: 1.75rem;
  padding-inline: 1.75rem;
  background-color: var(--white);
}
@media (min-width: 40em) {
  section .info-ip {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.125rem;
    padding-block: 2.3125rem;
    padding-inline: 2rem;
    text-align: left;
  }
  section .info-ip > *:not(:last-child) {
    border-right: 0.0625rem solid #c9c9c9;
  }
}
section .info-ip div .title {
  display: block;
  margin-bottom: 0.9375rem;
  font-size: 0.5625rem;
  text-transform: uppercase;
  letter-spacing: 0.0625rem;
  color: var(--dk-gray);
}
@media (min-width: 71.875em) {
  section .info-ip div .title {
    margin-bottom: 1.3125rem;
    font-size: 0.6875rem;
  }
}
section .info-ip div .content {
  display: block;
  margin-bottom: 1.9375rem;
  font-size: 0.9375rem;
  font-weight: 700;
  color: var(--very-dk-gray);
}
@media (min-width: 71.875em) {
  section .info-ip div .content {
    font-size: 1.25rem;
  }
}